@import url(https://fonts.googleapis.com/css?family=Roboto);
* {
  font-family: 'Roboto', sans-serif; }

body {
  padding: 0;
  margin: 0;
  background-color: #bbb; }

button {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit; }

.button--base {
  font-size: 30px; }

.button-number {
  background-color: white;
  border: 2px solid white;
  font-weight: bold; }
  .button-number:hover {
    border: 2px solid white;
    background-color: gray; }

.button-operation {
  background-color: lightgray;
  border: 2px solid lightgray; }
  .button-operation:hover {
    border: 2px solid lightcyan;
    background-color: lightblue; }

.button-option {
  border: 2px solid lightgray;
  background-color: lightgray; }
  .button-option:hover {
    border: 2px solid lightgray;
    background-color: gray; }

.calculator {
  height: 50vh;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-column-gap: 3px;
  -webkit-column-gap: 3px;
          column-gap: 3px;
  grid-row-gap: 3px;
  row-gap: 3px; }
  @media (max-width: 1024px) {
    .calculator {
      width: 90vw; } }
  @media (min-width: 1025px) {
    .calculator {
      width: 50vw; } }

.container {
  padding: 0;
  width: 100vw;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center; }

.memory {
  background-color: #454545;
  color: white;
  text-align: center; }

.visor {
  display: block;
  word-wrap: break-word;
  font-size: 40px;
  text-align: right;
  padding-right: 10px;
  grid-column-start: 2;
  grid-column-end: 5;
  background-color: #545454;
  color: white; }

